@import "mixin";

/* icons */
@import "icons";

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 50px $block-bg inset;
  -webkit-text-fill-color: $dark; }
input:-webkit-autofill::first-line {
  font-size: 16px;
  -webkit-font-size: 16px;
  line-height: 24px;
  font-family: inherit;
 }  // font-weight: 400

// *::-webkit-scrollbar
//   width: 4px
// *::-webkit-scrollbar-track
//   background: yellow
// *::-webkit-scrollbar-thumb
//   background-color: red
//   border-radius: 50px
// *::-webkit-scrollbar-thumb:hover
//    background: lighten(red, 10)

* {
  &::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    cursor: pointer; }
  &::-webkit-scrollbar-track {
    // box-shadow: inset 0 0 5px lighten($orange, 55%)
    background-color: lighten($btn-bg, 20%); }
  &::-webkit-scrollbar-thumb {
    background-color: lighten($btn-bg, 10%);
    border-radius: 50px; }
  &::-webkit-scrollbar-thumb:hover {
    background: lighten($btn-bg, 5%); } }

.rate-pay-form {
  -moz-user-select: none;
  -o-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.page-content {
  max-width: 1330px;
  width: calc( 100% - (56px + 56px));
  margin-left: auto;
  margin-right: auto;
  background-color: $clear;
  border-radius: $border-radius;
  margin-top: 110px;
  margin-bottom: 30px;
  padding: 60px 80px;
  @include media-breakpoint-down(sm) {
    width: calc( 100% - 30px);
    padding: 30px 15px; } }

h1,h2,h3,h4,h5 {
  color: $title-c;
  // font-family: $title
  font-weight: 500;
  font-style: normal; }
.page-title {
  margin-bottom: 80px;
  @include media-breakpoint-down(md) {
