// hard disabled links
[href='#none'] {
 }  // pointer-events: none


/* Dropdown */
.dropdown {
  position: relative;
  &-toggle {
    background-color: $clear;
    border: none;
    &::after {
      display: none; }
    .img-wrap {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      vertical-align: middle;
      border-radius: 50%;
      background-color: $site-bg;
      width: 32px;
      min-width: 32px;
      height: 32px;
      font-size: 12px;
      color: $ava-txt;
      overflow: hidden;
      /* if txt */
      text-transform: uppercase;
      /* img */
      .user-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center top; } }
    .name {
      font-size: 13px; }
    .icon-brecket {
      .brecket {
        transition: .3s; } } }
  &-menu {
    position: absolute;
    top: 100%;
    left: 15px;
    width: 100%;
    border: none;
    display: block;
    max-height: 0;
    overflow: hidden;
    padding: 0;
    background-color: $clear;
    border-radius: $border-radius-lang-wr;
    box-shadow: 0px 4px 8px rgba(149, 167, 175, 0.24);
    z-index: 50;
    transition: .5s;
    .dropdown-item {
      display: block;
      color: $text-c;
      padding: 10px;
      font-size: 15px;
      &:hover {
        background-color: $block-bg; } } } }
.dropdown.show {
  .icon-brecket {
    .brecket {
      transform: rotateX(-180deg);
      transform-origin: center; } }
  .dropdown-menu {
    padding-top: 10px;
    max-height: 2000px;
    transition: max-height 2s;
    &:hover {
      .active {
        background-color: $clear; } }
    .active {
      background-color: $block-bg;
      &:hover {
        background-color: $block-bg; } } } }

