/*  input type="number" */
.quantity {
  display: inline-flex;
  background-color: $clear;
  padding: 12px;
  border-radius: $border-radius-lang;
  margin-right: 30px;
  user-select: none;
  &-button {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    width: 24px;
    height: 24px;
    border: $b $block-bord;
    border-radius: $border-radius-lang;
    color: $btn-num;
    font-size: 20px;
 }    // margin-bottom: 15px
  &-number {
    font-size: 15px;
    font-weight: 500;
    margin: 0 10px;
    .month {
      font-size: 12px; } } }

.btn-rate-pay {
  display: inline-block;
  vertical-align: bottom;
  background-color: $btn-bg;
  border-radius: $border-radius;
  font-size: 15px;
  font-weight: 500;
  line-height: 24px;
  color: $clear;
  text-align: center;
  padding: 12px 83px;
  border: 1.5px solid $btn-bg;
  transition: background-color .4s;
  @include media-breakpoint-down(sm) {
    padding: 12px 15px; }
  &:hover {
    background-color: darken($btn-bg, 5%); }
  &:focus {
    background-color: darken($btn-bg, 10%); } }


ul {
  padding: 5px 0 0 0;
  margin: 0;
  li,
  li>a {
    list-style: none;
    margin: 0;
    padding: 0 0 10px 0;
    // font-family: $text
    font-size: 16px;
    line-height: 26px;
    font-weight: 400; } }

input,
textarea {
  &::placeholder {
    transition: .4s; }
  &:focus {
    &::placeholder {
      font-size: 0;
      color: transparent; } } }

/* Error-Message */
.error-message {
  position: absolute;
  font-size: 12px;
  bottom: -20px;
  left: 15px;
  color: $error;
  font-weight: 500;
  transition: .3s;
  @include media-breakpoint-down(sm) {
    left: 0;
    font-size: 11px; } }

.site-btn,
.site-btn-lite {
  width: 100%;
  min-height: 48px;
  padding: 12px;
  font-size: 15px;
  line-height: 24px;
  font-weight: 500;
  text-align: center;
  color: $clear;
  background-color: $btn-bg;
  border-radius: $border-radius;
  transition: .3s;
  @include media-breakpoint-down(sm) {
    font-size: 14px;
    line-height: 20px;
    padding: 10px; }
  &:hover {
    background-color: darken($btn-bg, 10); }
  &:focus {
    background-color: darken($btn-bg, 15); }
  &[disabled] {
    background-color: $btn-off-bg;
    border-color: $btn-off-bg;
    color: $dark; } }
.site-btn-lite {
  color: $dark;
  background-color: transparent;
  border: 1.5px solid $btn-bord;
  &:hover {
    background-color: transparentize($btn-bg, .9); }
  &:focus {
    background-color: transparentize($btn-bg, .8); } }


