/* Dropdown */
.dropdown {
  position: relative; }
  .dropdown-toggle {
    background-color: #ffffff;
    border: none; }
    .dropdown-toggle::after {
      display: none; }
    .dropdown-toggle .img-wrap {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      vertical-align: middle;
      border-radius: 50%;
      background-color: #EDF0F8;
      width: 32px;
      min-width: 32px;
      height: 32px;
      font-size: 12px;
      color: #6982C5;
      overflow: hidden;
      /* if txt */
      text-transform: uppercase;
      /* img */ }
      .dropdown-toggle .img-wrap .user-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center top; }
    .dropdown-toggle .name {
      font-size: 13px; }
    .dropdown-toggle .icon-brecket .brecket {
      transition: .3s; }
  .dropdown-menu {
    position: absolute;
    top: 100%;
    left: 15px;
    width: 100%;
    border: none;
    display: block;
    max-height: 0;
    overflow: hidden;
    padding: 0;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0px 4px 8px rgba(149, 167, 175, 0.24);
    z-index: 50;
    transition: .5s; }
    .dropdown-menu .dropdown-item {
      display: block;
      color: #4B4751;
      padding: 10px;
      font-size: 15px; }
      .dropdown-menu .dropdown-item:hover {
        background-color: #F1F1F1; }

.dropdown.show .icon-brecket .brecket {
  transform: rotateX(-180deg);
  transform-origin: center; }

.dropdown.show .dropdown-menu {
  padding-top: 10px;
  max-height: 2000px;
  transition: max-height 2s; }
  .dropdown.show .dropdown-menu:hover .active {
    background-color: #ffffff; }
  .dropdown.show .dropdown-menu .active {
    background-color: #F1F1F1; }
    .dropdown.show .dropdown-menu .active:hover {
      background-color: #F1F1F1; }

